header {
  box-sizing: border-box;
  background-image: url("header-bg-moblile.0bb2d07e.png");
  background-position: center;
  background-size: cover;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  display: flex;
}

.header__waves {
  background-image: url("icon-waves.575b627a.png");
  width: 48px;
  height: 57px;
  margin-bottom: 16px;
}

.header__title {
  letter-spacing: -1.5px;
  margin: 0;
  font-size: 32px;
  font-style: italic;
  font-weight: 800;
  line-height: 100%;
}

.header__button {
  padding: 0 20px 40px;
}

.top-bar {
  justify-content: space-between;
  align-items: center;
  padding: 24px 20px;
  display: flex;
}

.top-bar__icon {
  gap: 24px;
  display: flex;
}

.top-bar__logo {
  display: flex;
}

.icon {
  width: 24px;
  height: 24px;
  display: block;
}

.icon--phone {
  background-image: url("icon-phone.17246ea8.png");
}

.icon--menu {
  background-image: url("icon-buger.73ba25c4.png");
}

.icon--close {
  background-image: url("close.0f4a9211.png");
}

.menu {
  box-sizing: border-box;
  text-transform: uppercase;
  background-color: #e5e5e5;
  height: 100vh;
  padding: 24px 20px 40px;
  font-weight: bold;
}

.menu__top {
  margin-bottom: 32px;
}

.menu__nav {
  margin-bottom: 40px;
}

.menu__content {
  padding-inline: 20px;
}

.menu__phone-number {
  color: inherit;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 27px;
  text-decoration: none;
  display: block;
}

.menu__call-to-order {
  letter-spacing: 2px;
  width: fit-content;
  color: inherit;
  border-bottom: 1px solid #292929;
  padding-bottom: 7px;
  font-size: 12px;
  line-height: 16px;
  text-decoration: none;
  display: block;
}

.nav__link {
  color: inherit;
  letter-spacing: 2px;
  font-size: 22px;
  line-height: 22px;
  text-decoration: none;
}

.nav__list {
  flex-direction: column;
  gap: 32px;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}
